.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 3px solid #0b0b0b;
  border-radius: 50%;
  margin: 20px auto;
  animation: animloader 2s linear infinite;
  display: block;
  position: relative;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 6px;
  height: 24px;
  background: #080000;
  position: absolute;
  bottom: -20px;
  left: 46px;
  transform: rotate(-45deg);
}

@keyframes animloader {
  0% {
    transform: translate(-10px, -10px);
  }

  25% {
    transform: translate(-10px, 10px);
  }

  50% {
    transform: translate(10px, 10px);
  }

  75% {
    transform: translate(10px, -10px);
  }

  100% {
    transform: translate(-10px, -10px);
  }
}

.scroll-up-btn {
  color: #fff;
  cursor: pointer;
  background-color: rgba(79, 46, 232, .6);
  border: none;
  border-radius: 36px;
  padding: 10px 20px;
  display: none;
  position: fixed;
  bottom: 100px;
  right: 20px;
}

.scroll-up-btn:hover, .scroll-up-btn:hover {
  background-color: #4f2ee8;
}

.footer-cont {
  justify-content: space-evenly;
  padding-top: 25px;
  padding-bottom: 25px;
  display: flex;
}

.loader-heart {
  width: 40px;
  height: 40px;
  animation: heartBeat 1.2s cubic-bezier(.215, .61, .355, 1) infinite;
  position: relative;
}

.loader-heart:before, .loader-heart:after {
  content: "";
  width: 40px;
  height: 60px;
  transform-origin: 50% 68%;
  background: #4f2ee8;
  border-radius: 50px 50px 0 0;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: rotate(45deg);
  box-shadow: inset 5px 4px 5px rgba(0, 0, 0, .267);
}

.loader-heart:after {
  transform: rotate(-45deg);
}

@keyframes heartBeat {
  0% {
    transform: scale(.95);
  }

  5% {
    transform: scale(1.1);
  }

  39% {
    transform: scale(.85);
  }

  45% {
    transform: scale(1);
  }

  60% {
    transform: scale(.95);
  }

  100% {
    transform: scale(.9);
  }
}

:root {
  --White: #fff;
  --Yellow: #eac645;
  --Blue: #4f2ee8;
  --Black: #202024;
  --Darker: #111;
}

body {
  background-color: var(--White);
  color: var(--Darker);
  margin: 0;
  font-family: DM Sans, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.9;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  width: 100%;
  padding: 0 20px;
}

@media screen and (min-width: 375px) {
  .container {
    width: 375px;
    margin: 0 auto;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    width: 1392px;
    padding: 0 24px;
  }
}

